
import { defineComponent, ref } from 'vue'
import LoginAccount from './panel/panel-account.vue'
import LoginPhone from './panel/panel-phone.vue'

export default defineComponent({
  name: '',
  components: { LoginAccount, LoginPhone },
  setup() {
    const isKeepPassWrod = ref(true)
    const currentTab = ref('account')
    const accountRef = ref<InstanceType<typeof LoginAccount>>()
    const loginRef = ref<InstanceType<typeof LoginPhone>>()
    function handleLoginClick() {
      if (currentTab.value == 'account') {
        accountRef.value?.loginActive(isKeepPassWrod.value) // 调用子组件的登录
      } else {
        loginRef.value?.loginActive(isKeepPassWrod.value) // 调用子组件的登录
      }
    }

    return {
      isKeepPassWrod,
      handleLoginClick,
      accountRef,
      loginRef,
      currentTab
    }
  }
})
